.character-details {
  min-height: 100vh;
}

.character-details .container {
  font-size: 1.3em;
  height: 100vh;
  background: rgb(255 255 255 / 80%);
  padding: 1em 2em;
  max-width: 400px;
  margin: auto;
}

.character-details .container h1 {
  color: var(--primary-color);
  border-bottom: 3px solid var(--primary-color);
  padding-bottom: 10px;
}

.character-details .container ul {
  padding: 0;
  list-style: inside;
  line-height: 1.7;
  text-transform: capitalize;
}

.character-details .container button {
  position: absolute;
  bottom: 5em;
  font-size: 14px;
}

.character-details .container ul li ul {
  padding-left: 18px;
  list-style-type: circle;
  font-size: 16px;
}
